// import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routers'
import store from '@/store'
import iView from 'iview'
import { canTurnTo } from '@/libs/util'

// Vue.use(Router);
const router = new VueRouter({
  mode: 'history',
  routes
});

const LOGIN_PAGE_NAME = 'login';
router.beforeEach((to, from, next) => {
  iView.LoadingBar.start();
  // const token = store.state.user.username;
  const token = localStorage.getItem('username');
  // const token = store.getters.user.getUsername;
  if (!token && to.name !== LOGIN_PAGE_NAME) {
    // 
    next({
      name: LOGIN_PAGE_NAME // 
    })
  } else if (!token && to.name === LOGIN_PAGE_NAME) {
    // 
    next() // 
  } else if (token && to.name === LOGIN_PAGE_NAME) {
    // 
    next({
      name: 'home' // home
    })
  } else {
    let access = localStorage.getItem('roles');
    // name;access['super_admin'] ['super_admin', 'admin']
    if (canTurnTo(to.name, access, routes)) next(); // 
    else next({replace: true, name: 'error_401'}) // 401
  }
});

router.afterEach(to => {
  iView.LoadingBar.finish();
  window.scrollTo(0, 0)
});
export default router



