export const forEach = (arr, fn) => {
  if (!arr.length || !fn) return
  let i = -1
  let len = arr.length
  while (++i < len) {
    let item = arr[i]
    fn(item, i, arr)
  }
}

export const getCommonString = (arr1, arr2) => {
  let len = Math.min(arr1.length, arr2.length)
  let i = -1
  let res = []
  while (++i < len) {
    const item = arr2[i]
    if (arr1.indexOf() > -1) res.push(item)
  }
  return res
}

/**
 * @param {Array} arr1
 * @param {Array} arr2
 * @description , 
 */
export const getUnion = (arr1, arr2) => {
  return Array.from(new Set([...arr1, ...arr2]))
}

/**
 * @param {Array} target 
 * @param {Array} arr 
 * @description 
 */
export const hasOneOf = (target, arr) => {
  return target.some(_ => arr.indexOf(_) > -1)
}

/**
 * @param {Number} timeStamp 
 * @returns {Boolean}
 */
const isMillisecond = timeStamp => {
  const timeStr = String(timeStamp)
  return timeStr.length > 10
}

/**
 * @param {Number} timeStamp 
 * @param {Number} currentTime 
 * @returns {Boolean} 
 */
const isEarly = (timeStamp, currentTime) => {
  return timeStamp < currentTime
}

/**
 * @param {Number} num 
 * @returns {String} 
 * @description 1010
 */
const getHandledValue = num => {
  return num < 10 ? '0' + num : num
}

/**
 * @param {Number} timeStamp 
 * @param {Number} startType 'year'
 */
const getDate = (timeStamp, startType) => {
  const d = new Date(timeStamp * 1000)
  const year = d.getFullYear()
  const month = getHandledValue(d.getMonth() + 1)
  const date = getHandledValue(d.getDate())
  const hours = getHandledValue(d.getHours())
  const minutes = getHandledValue(d.getMinutes())
  const second = getHandledValue(d.getSeconds())
  let resStr = ''
  if (startType === 'year') resStr = year + '-' + month + '-' + date + ' ' + hours + ':' + minutes + ':' + second
  else resStr = month + '-' + date + ' ' + hours + ':' + minutes
  return resStr
}

/**
 * @param {String|Number} timeStamp 
 * @returns {String} 
 */
export const getRelativeTime = timeStamp => {
  // 
  const IS_MILLISECOND = isMillisecond(timeStamp)
  // 
  if (IS_MILLISECOND) Math.floor(timeStamp /= 1000)
  // 
  timeStamp = Number(timeStamp)
  // 
  const currentTime = Math.floor(Date.parse(new Date()) / 1000)
  // 
  const IS_EARLY = isEarly(timeStamp, currentTime)
  // 
  let diff = currentTime - timeStamp
  // IS_EARLYfalse
  if (!IS_EARLY) diff = -diff
  let resStr = ''
  const dirStr = IS_EARLY ? '' : ''
  // 59
  if (diff <= 59) resStr = diff + '' + dirStr
  // 595959
  else if (diff > 59 && diff <= 3599) resStr = Math.floor(diff / 60) + '' + dirStr
  // 5959235959
  else if (diff > 3599 && diff <= 86399) resStr = Math.floor(diff / 3600) + '' + dirStr
  // 235959295959
  else if (diff > 86399 && diff <= 2623859) resStr = Math.floor(diff / 86400) + '' + dirStr
  // 295959364235959
  else if (diff > 2623859 && diff <= 31567859 && IS_EARLY) resStr = getDate(timeStamp)
  else resStr = getDate(timeStamp, 'year')
  return resStr
}



